<div id="mobile-div" class="outer-container">
  <div class="majorView">
    <div class="banner">
        <img src="icons/banner.png" width="100%"/>
    </div>
    <div class="title">
      <div class="major-title">
        <h2>疫情深度分析</h2>
      </div>
      <!-- <div class="sub-title">
        <h5>数据来源：国家及各省市卫健委</h5>
      </div> -->
    </div>
    <div class="content">
      <div v-for="item in entries" :key="item.id" :href="item.link" class="container" :style="{backgroundColor:item.background}">
        <div class="icon-wrapper"><div class="fig-wrapper"><img :src="item.imgsrc" height="100%"/></div></div>
        <div class="text-wrapper">
          <div><h3 :style="{color:item.title}">{{item.name}}</h3></div>
         <div><p :style="{color:item.title}">{{item.content}}</p></div>
        </div>
        <!-- <div class="seperaterline"></div> -->
          <!-- <div class="detail-wrapper"><p >{{item.content}}</p></div> -->
        <div class="icon-entry"> <a :href="item.link">
          <div class="launch-wrapper fig-wrapper">
            <img :src="item.launch"/>
          </div>
          </a>

        </div>
      </div>
    </div>
        </div>
      <div class="footer">
        <div style="text-align:center;">
          <a href="https://vis.ucloud365.com/ncov/">
             VIS@PKU更多疫情可视化工作 > </a>
          <p>若您需要合作，请联系我们</p>
        </div>
      </div>

  </div>
</template>

<script>
import * as d3 from 'd3'
import * as data from '../assets/entrylist.js'
export default {
  name: "Mobile",
  data() {
    return {
      entries: []
    }
  },
  created() {
    this.init()
  },
  mounted() {
    this.entries = data.entries
    d3.selectAll(".img-container").on("touchstart", function() {
      let self = d3.select(this)
      self.on('mouseenter')
      setTimeout(function() {
        self.on('mouseout')
      }, 200)
    })
    d3.selectAll(".img-container").on("touchend", function() {
      d3.select(this).on('mouseout')
    })
    d3.selectAll(".img-container").on("tap", function() {
      let url = d3.select(this).attr('href')
      setTimeout(function() {
        window.location.href = url
      }, 500)
    })
  },
  methods: {
    init() {
      let that = this;
      let forPhone = that.forPhone
      forPhone = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) || forPhone
      that.forPhone = forPhone
      d3.select(that.$el).classed('forPhone', forPhone)
      let ua = navigator.userAgent.toLowerCase();
      let fontFamily = 'Roboto, SourceHanSansCN, “思源黑体”, Noto Sans SC'
      if (/\(i[^;]+;( U;)? CPU.+Mac OS X/gi.test(ua)) {
        fontFamily = 'PingFang SC'
      }
      d3.select('body').style('font-family', fontFamily)
    },
    updateEntries: function(data) {
      this.entries = data
    }
  }
}
</script>
